.App {
  text-align: center;
}
.logo-img {
  display: block;
  width: 300px;
  height: auto;
  float: left
}
.header {
  display: block;
  width: 100%;
}
.header-name {
  color: blue;
  text-align: right;
  margin-right: 20px;
}
.header-title {
  font-size: 13px;
  color: skyblue;
}
.navbar-nav {
  text-align: center;
  margin: 5px 0;
}
.navbar-nav li {
  display: inline-block;
  width: 30%;
  text-decoration: none !important;
}
.nav-link {
  position: relative;
  display: inline-block;
  padding: 15px 30px;
  color: #2196f3;
  text-transform: uppercase;
  letter-spacing: 4px;
  text-decoration: none;
  font-size: 24px;
  overflow: hidden;
  transition: 0.2s;
}
.nav-link:hover
{
    color: #255784;
    background: #2196f3;
    box-shadow: 0 0 10px #2196f3, 0 0 40px #2196f3, 0 0 80px #2196f3;
    transition-delay: 1s;
}
.nav-link span
{
    position: absolute;
    display: block;
}
.nav-link span:nth-child(1)
{
    top: 0;
    left: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg,transparent,#2196f3);
}

.nav-link:hover span:nth-child(1)
{
    left: 100%;
    transition: 1s;
} 
.nav-link span:nth-child(2)
{
    bottom: 0;
    right: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(270deg,transparent,#2196f3);
}
.nav-link:hover span:nth-child(2)
{
    right: 100%;
    transition: 1s;
    transition-delay: 0.5s;
}
.nav-link span:nth-child(3)
{
    top: -100℅;
    right: 0%;
    width: 2px%;
    height: 100%;
    background: linear-gradient(180deg,transparent,#2196f3);
}
.nav-link:hover span:nth-child(3)
{
    top: 100%;
    transition: 1s;
    transition-delay: 0.25s;
}
.nav-link span:nth-child(4)
{
    bottom: -100℅;
    left: 0%;
    width: 2px%;
    height: 100%;
    background: linear-gradient(360deg,transparent,#2196f3);
}
.nav-link:hover span:nth-child(4)
{
    bottom: 100%;
    transition: 1s;
    transition-delay: 0.75s;
}
hr {
  border-color: blue;
}

.container {
  color: blue;
}

@media only screen and (max-width: 600px) {
  .navbar-nav {
    padding: 0;
  }
  .nav-link {
    padding: 5px 2px;
  }
}